import React ,{useEffect, useState}from 'react'
import './single.css';
import InfoLogo from './img/info.png';
import bookmark from './img/save.png';
import door from './img/door.png';
import share from './img/share.png';
import heartfull from './img/heart.png';
import heartdrop from './img/hdrop.png'
import factory from './img/factory.png';
import comunity from'./img/commiunity.png';
import group from './img/group.png';
import ReactTooltip from "react-tooltip";
import insta from './img/instagram.png';
import axios from 'axios';
import { useMemo } from 'react';
import { useHistory } from 'react-router-dom';

const SingleDesign = ({getUserSavedImages, part, show, image}) => {
    const history = useHistory()
    const[user, setUser] = useState()
    const[loader, setLoader] = useState(false)
    let {post_image,community_likes,profile_image,instaLikes,factory_likes,group_likes,post_on}= part;
    const showsing=show;
    const [comment,setComment]=useState('');
    const onchange=(e)=>{
        setComment(e.target.value)
        e.preventDefault();
    }

    const submitComment = async(id) => {
        console.log(id, comment)
        try {
            const res = await fetch("/userImgComment", {
                method: "POST",
                headers: {
                    "Content-type": "application/json"
                },
                body: JSON.stringify({_id: id, comment})
            })
            const data = await res.json()
            console.log(res)
            setComment("")
            getUserSavedImages()
            setLoader(false)
        } catch (error) {
            setLoader(false)
            console.error(error)
            history.push("/login")
        }
    }

    useMemo(() => {
        console.log(image)
    }, [submitComment])

    const cumunitydrop=(e)=>{
        // e.target.src={heartdrop};
        const heartchnge= document.querySelector('#cumunitydrop')
        heartchnge.src=heartdrop;
        heartchnge.style.width='20px';
        heartchnge.style.height='20px';
        heartchnge.style.margin='0 5px';
        community_likes=community_likes-1;
        document.querySelector('#comunitylike').innerText=community_likes;
        e.preventDefault();
        console.log('heartdrop')
        console.log(showsing ? 'yes' : 'no')
    }

    const getUserProfileInfo=async()=>{
        await axios.get(`/userProfile`)
        .then((res) => {
            if(res.status===200){
                console.log(res.data)
                setUser(res.data)
            }
        }).catch((err) => {
            console.log(err)
        })
    }

    useEffect(() => {
        getUserProfileInfo()
    },[])

  return (
    <div className='singledesign'>
       <div className='sdesignimg'>
           <img src={image.designer_imgs} className='img-fluid'/>
       </div>
       <div className='sdesigndetail'>
           <div className='sdesignhead'>
               <img src={user&&user.file?"/images/"+user.file: "/images/avt.jpg"} alt='profile pic' width='45px' height='45px' />
               <h6>{user&&user.name}</h6>
           </div>
           <div className='sdescription1' id='designsingledescription'>
               <p>{image.description}</p>
           </div>
           <div className='scomments1' id='designsinglecomment'>
              <p>Comments</p>
              {/* <div className='spcomments'>
                    <img src={image.designer_imgs} alt='profile pic' width='35px' height='35px' />
                    <div className='authornames'>
                        <h6>Mr.sheep</h6>
                         <p>This is good one</p>
                    </div>   
                </div> */}
                {image&&image.comments?image.comments.map((comment) => (
                    <div className='spcomments' key={comment._id}>
                        <img src={comment.user_img?"/images/"+comment.user_img: "/images/avt.jpg"} alt='profile pic' width='35px' height='35px' />
                        <div className='authornames'>
                            <h6>{comment.user_name}</h6>
                            <p>{comment.comment}</p>
                        </div>   
                    </div>
                )): "No Comments"}
           </div>
           <div className='commentinput'>
               <form>
                   <input type='text' placeholder='Write your comments' value={comment} onChange={onchange} />
               </form>
           </div>
           <div className='publisheddetails'>
               <div className='pubdate'>
                   <h5>Published on {new Date(image&&image.createdAt).toLocaleDateString()}</h5>
               </div>
               <div className='pubdetails'>
                <img src={InfoLogo} style={{cursor: "pointer"}} height='30px' width='30px' />
                <img src={bookmark} style={{cursor: "pointer"}} height='30px' width='30px' />
                <img src={door} style={{cursor: "pointer"}} height='35px' width='35px' />
                <img src={share} style={{cursor: "pointer"}} height='35px' width='35px' onClick={()=>submitComment(image._id)} />
               </div>
           </div>
           <div className='cfoot'>
            <div className='frow'>
                <div className='footicongroup' >
                                    <div>
                                    <img src={comunity} height='35px' width='34px' data-tip data-for="registerTipo7" data-arrow-color='white' />
                                    <ReactTooltip id="registerTipo7" place="top"   effect="solid" >
                                    These are the likes of of our community
                                        </ReactTooltip>
                                        </div>
                                    
                                        <img src={heartfull} height='30px' width='30px' className='my'  data-tip data-for="registerTipo8" data-arrow-color='white'/>
                                        <ReactTooltip id="registerTipo8" place="top"   effect="solid" >
                                    These are the likes of of our community
                                        </ReactTooltip>
                                    <span style={{marginTop:"0px", fontSize:"26px"}} data-tip data-for="registerTipo9" data-arrow-color='white'>
                                    {image.com_likes&&
                                        Math.abs(parseInt(image.com_likes.length))>=1000?
                                        Math.sign(parseInt(image.com_likes.length))*((Math.abs(parseInt(image.com_likes.length))/1000).toFixed(1))+"k" :
                                        Math.sign(parseInt(image.com_likes.length))*Math.abs(parseInt(image.com_likes.length))
                                    }
                                    </span> <ReactTooltip id="registerTipo9" place="top"   effect="solid" >
                                    These are the likes of of our community
                                        </ReactTooltip>
                </div>
                
                <div className='footicongroup'>
                                <div>
                                    <img src={factory} height='35px' width='34px' data-tip data-for="registerTipo10" data-arrow-color='white'/>
                                        <ReactTooltip id="registerTipo10" place="top"   effect="solid" >
                                        These counts the manufactureres who participate
                                            </ReactTooltip>
                                </div>
                                    
                                        <img src={heartfull} height='30px' width='30px' className='my' data-tip data-for="registerTipo11" data-arrow-color='white'/>
                                        <ReactTooltip id="registerTipo11" place="top"   effect="solid" >
                                    These counts the manufactureres who participate 
                                        </ReactTooltip>
                                    <span style={{marginTop:"0px", fontSize:"26px"}} data-tip data-for="registerTipo12" data-arrow-color='white'>{part.factory_likes}</span> <ReactTooltip id="registerTipo12" place="top"   effect="solid" >
                                    These counts the manufactureres who participate 
                                        </ReactTooltip>
                                        </div>
                                   </div>
                                   <div className='frow'>
                                        <div className='footicongroup'>
                                            <div>
                                                 <img src={insta} height='35px' width='34px' data-tip data-for="registerTipo13" data-arrow-color='white'/>
                                                                <ReactTooltip id="registerTipo13" place="top"   effect="solid" >
                                                                These are the likes  at Instagram
                                                                </ReactTooltip>
                                            </div>
                                                          
                                                                <img src={heartfull} height='30px' width='30px' className='my'  data-tip data-for="registerTipo14" data-arrow-color='white'/>
                                                                <ReactTooltip id="registerTipo14" place="top"   effect="solid" >
                                                                These are the likes  at Instagram
                                                                </ReactTooltip>
                                                            <span style={{marginTop:"0px", fontSize:"26px"}} data-tip data-for="registerTipo15" data-arrow-color='white'>
                                                                {image.instaLikes&&
                                                                        Math.abs(parseInt(image.instaLikes))>=1000?
                                                                        Math.sign(parseInt(image.instaLikes))*((Math.abs(parseInt(image.instaLikes))/1000).toFixed(1))+"k" :
                                                                        Math.sign(parseInt(image.instaLikes))*Math.abs(parseInt(image.instaLikes))
                                                                }</span> <ReactTooltip id="registerTipo15" place="top"   effect="solid" >
                                                            These are the likes  at Instagram
                                                                </ReactTooltip>
                                        </div>
                                        <div className='footicongroup'>
                                           <div>
                                                 <img src={group} height='35px' width='34px' data-tip data-for="registerTipo16" data-arrow-color='white'/>
                                                                <ReactTooltip id="registerTipo16" place="top"   effect="solid" >
                                                                These are the likes of the jury
                                                                </ReactTooltip>
                                            </div>
                                                            
                                                                <img src={heartfull} height='30px' width='30px' className='my' data-tip data-for="registerTipo17" data-arrow-color='white'/>
                                                                <ReactTooltip id="registerTipo17" place="top"   effect="solid" >
                                                                These are the likes of the jury
                                                                </ReactTooltip>
                                                            <span style={{marginTop:"0px", fontSize:"26px"}} data-tip data-for="registerTip15" data-arrow-color='white'>
                                                            {image.jury_likes&&
                                                                Math.abs(parseInt(image.jury_likes.length))>=1000?
                                                                Math.sign(parseInt(image.jury_likes.length))*((Math.abs(parseInt(image.jury_likes.length))/1000).toFixed(1))+"k" :
                                                                Math.sign(parseInt(image.jury_likes.length))*Math.abs(parseInt(image.jury_likes.length))
                                                            }
                                                            </span> <ReactTooltip id="registerTip15" place="top"   effect="solid" data-event-off='hover'>
                                                            These are the likes of the jury
                                                                </ReactTooltip>
                                        </div>
                                   </div>
                               </div>
       </div>
    </div>
  )
}

export default SingleDesign