import React, { useEffect, useState } from 'react'
import Header from '../header/Header'
import Footer from '../footer/Footer'
import { fetchdesigndata } from '../../redux/designgallery/DesignActions';
import { connect } from 'react-redux'
import DesignComponent from './DesignComponent';
import axios from 'axios';
import { useHistory, useParams } from 'react-router-dom';
import { Spinner } from 'react-bootstrap';

const DesginG = (props) => {
  const params = useParams()
  const history = useHistory()
  const[user, setUser] = useState()

  const getUserSavedImages=async()=>{
    const id = params.id
    const result = await axios.get(`/getAllDesignerImages`)
      .then((res) => {
        const {data} = res
        setUser(data)
      }).catch((err) => {
        console.log(err)
        history.push("/login")
      })
}

useEffect(() => {
    getUserSavedImages()
},[])

  return (
    <div>
        <Header />
       <div className='designgallery'>
         <div className='designheading'>
             <h1>Design Gallery</h1>
         </div>
         <div className='designsort'>
             <form>
                <label>Sort By:</label>
                <input type='text' name='search' placeholder='Keyword'/>
             </form>
         </div>
         <div className='maingallery'>
              {user?user.map((image) => (
                <DesignComponent getUserSavedImages={getUserSavedImages} image={image}/> 
              )):
              <Spinner animation="border" role="status">
                  <span className="visually-hidden">Loading...</span>
              </Spinner>
              }
         </div>
        </div>
        <Footer />  
    </div>
  )
}

const mapStateToProps=(state)=>{
    return{
        designdata:state.design.designdata
    }
  
}
const mapDispatchToProps=dispatch=>{
    return{
      fetchdata:()=>{
        dispatch(fetchdesigndata());
      }
    }
  }

export default  connect(mapStateToProps,mapDispatchToProps)(DesginG)