import React, { useEffect, useState } from 'react'
import comunity from '../img/comunity.png'
import ReactTooltip from 'react-tooltip'
import insta from '../img/insta.png'
import jury from '../img/jury.png'
import corp from '../img/corp.png'
import Popup from 'reactjs-popup';
import SingleDesign from './singledesign/SingleDesign'
import './designg.css'
import axios from 'axios'
import { Spinner } from 'react-bootstrap'
const DesignComponent = ({image, getUserSavedImages}) => {
    const[isSaved, setIsSaved] = useState(false)
    const[user, setUser] = useState()
    const[loader, setLoader] = useState(false)
    let design = {}

    const getUserProfileInfo=async()=>{
        await axios.get("/userProfile")
        .then((res) => {
            if(res.status===200){
                console.log(res.data)
                setUser(res.data)
            }
        }).catch((err) => {
            console.log(err)
        })
    }

    useEffect(() => {
        getUserProfileInfo()
    },[])

  const saveImage = async(image) => {
    setIsSaved(true)
    console.log(image.description)
    setLoader(true)
    console.log("Saving...")
    try {
        const res = await fetch("/savedUserDesignerImage", {
            method: "POST",
            headers: {
                "Content-type": "application/json"
            },
            body: JSON.stringify({d_image: image.designer_imgs, description: image.description, title: image.title})
        })
        const data = await res.json()
        console.log(data)
        // if(data.status !== 200) return history.push("/login")
        setLoader(false)
    } catch (error) {
        setLoader(false)
        console.error(error)
        alert("Please register first to save images.")
    }
  }

  const likeDesignerImage = async(image) => {
    let liked=true;
    let user_id;
    image&&image.com_likes&&image.com_likes.map((like) => {
        if(user&& user._id===like.user_id){
            liked = false
        }
    })
    console.log(liked)
    try {
        const res = await fetch("/likeDesignerImage", {
            method: "POST",
            headers: {
                "Content-type": "application/json"
            },
            body: JSON.stringify({_id: image._id, liked: liked?"true":"false", user_id})
        })
        const data = await res.json()
        // if(data.status !== 200) return history.push("/login")
        getUserSavedImages()
        console.log(data)
    } catch (error) {
        console.error(error)
        alert("Please register first to like image.")
    }
  }

  return (
    <div>
         <div className='designpart'>
                 <div className='mainimage'>
                 <img id={image._id} src={image.designer_imgs&&image.designer_imgs} alt="" />
                 
                    <div className='imagehover'>
                        <div className='hoverhead'>
                            <div className='infosvg' style={{cursor: "pointer"}}>
                                <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                                    <path d="M11.9802 7.89987C12.4957 7.89987 12.9135 7.482 12.9135 6.96654C12.9135 6.45107 12.4957 6.0332 11.9802 6.0332C11.4647 6.0332 11.0469 6.45107 11.0469 6.96654C11.0469 7.482 11.4647 7.89987 11.9802 7.89987Z" fill="#FAFAFA"/>
                                    <path d="M14.0007 16.6671H12.6673V9.40039H10.6673C10.4905 9.40039 10.3209 9.47063 10.1959 9.59565C10.0709 9.72068 10.0007 9.89025 10.0007 10.0671C10.0007 10.2439 10.0709 10.4134 10.1959 10.5385C10.3209 10.6635 10.4905 10.7337 10.6673 10.7337H11.334V16.6671H10.0007C9.82384 16.6671 9.65427 16.7373 9.52925 16.8623C9.40422 16.9873 9.33398 17.1569 9.33398 17.3337C9.33398 17.5105 9.40422 17.6801 9.52925 17.8051C9.65427 17.9302 9.82384 18.0004 10.0007 18.0004H14.0007C14.1775 18.0004 14.347 17.9302 14.4721 17.8051C14.5971 17.6801 14.6673 17.5105 14.6673 17.3337C14.6673 17.1569 14.5971 16.9873 14.4721 16.8623C14.347 16.7373 14.1775 16.6671 14.0007 16.6671Z" fill="#FAFAFA"/>
                                    <path d="M11.9987 22.6663C9.88904 22.6663 7.82674 22.0408 6.07262 20.8687C4.3185 19.6966 2.95132 18.0307 2.14399 16.0816C1.33665 14.1326 1.12542 11.9878 1.53699 9.91872C1.94857 7.84959 2.96447 5.94897 4.45623 4.45721C5.94799 2.96545 7.84861 1.94955 9.91774 1.53797C11.9869 1.12639 14.1316 1.33763 16.0807 2.14496C18.0297 2.9523 19.6956 4.31947 20.8677 6.0736C22.0398 7.82772 22.6654 9.89001 22.6654 11.9997C22.6654 14.8287 21.5416 17.5418 19.5412 19.5422C17.5408 21.5425 14.8277 22.6663 11.9987 22.6663V22.6663ZM11.9987 2.66635C10.1527 2.66635 8.34824 3.21374 6.81338 4.2393C5.27852 5.26486 4.08224 6.72252 3.37583 8.42797C2.66941 10.1334 2.48458 12.01 2.84471 13.8205C3.20484 15.631 4.09375 17.2941 5.39904 18.5993C6.70433 19.9046 8.36737 20.7935 10.1779 21.1537C11.9883 21.5138 13.865 21.329 15.5704 20.6226C17.2759 19.9161 18.7335 18.7199 19.7591 17.185C20.7846 15.6501 21.332 13.8456 21.332 11.9997C21.332 9.52433 20.3487 7.15035 18.5984 5.40002C16.848 3.64968 14.4741 2.66635 11.9987 2.66635V2.66635Z" fill="#FAFAFA"/>
                                </svg>
                                <h6>Learn more</h6>

                            </div>
                            <div className='hoverheadright'>
                                {loader?<>
                                    <Spinner style={{color: "green"}} animation="border" role="status">
                                        <span className="visually-hidden">Loading...</span>
                                    </Spinner>
                                </>: ""}
                                <div className='bookmark' style={{cursor: "pointer"}} onClick={()=>saveImage(image)} >
                                    <svg width="17" height="26" viewBox="0 0 17 26" fill={isSaved?"red":"none"} xmlns="http://www.w3.org/2000/svg">
                                    <path d="M16 25L8.5 15.6829L1 25V1H16V25Z" stroke="#FAFAFA" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
                                    </svg>
                                </div>
                                <div className='bookmark' style={{cursor: "pointer"}}>
                                    <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                                        <path d="M8.5 11H20.1L16.7 7.6L18.1333 6.16667L24 12.0333L18.2 17.8333L16.7667 16.4L20.1667 13H8.5V11ZM11.7 0V2H2V22H11.7V24H2C1.46667 24 1 23.8 0.6 23.4C0.2 23 0 22.5333 0 22V2C0 1.46667 0.2 1 0.6 0.6C1 0.2 1.46667 0 2 0H11.7Z" fill="white"/>
                                    </svg>
                                </div>
                            </div>
                        </div>
                        <div className='hoverbottom'>
                            <div className='hbottoml'>
                                <h4>Project Name</h4>
                            </div>
                            <div className='hbottomr'>
                                <div className='icons' style={{cursor: "pointer"}}
                                    onClick={()=>likeDesignerImage(image)}
                                >
                                    <svg width="34" height="35" viewBox="0 0 34 35" fill={
                                        image&&image.com_likes?
                                        image.com_likes.filter(like => user?._id===like.user_id).length===1?"red":"none":"none"
                                    } xmlns="http://www.w3.org/2000/svg">
                                        <path d="M17.0307 9.87222C16.3348 8.46535 15.1641 7.34965 13.7254 6.72213C12.2867 6.09462 10.6725 5.99567 9.16797 6.44274C7.6634 6.88982 6.36519 7.85416 5.5026 9.16548C4.64001 10.4768 4.26853 12.0508 4.45375 13.6094C4.45375 17.8766 7.66987 23.4913 17.0307 29.2588C26.3198 23.5003 29.5628 17.8766 29.5628 13.6094C29.708 12.0732 29.3157 10.5342 28.4529 9.25492C27.5901 7.97565 26.3102 7.03534 24.8315 6.59438C23.3528 6.15342 21.7669 6.23913 20.3444 6.83688C18.9218 7.43463 17.7507 8.50741 17.0307 9.87222V9.87222Z" stroke="white" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
                                    </svg>
                                    
                                </div>
                                <div className='icons' style={{cursor: "pointer"}}>
                                    <svg width="32" height="33" viewBox="0 0 32 33" fill="none" xmlns="http://www.w3.org/2000/svg">
                                        <path d="M25.9991 4.5L18.5591 28.5L13.2534 17.2543L25.9991 4.5Z" stroke="#FAFAFA" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
                                        <path d="M26 4.5L2 11.94L13.2543 17.2543L26 4.5Z" stroke="#FAFAFA" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
                                    </svg>
                                </div>
                                <div className='icons' style={{cursor: "pointer"}}>
                                    {/* <svg width="32" height="33" viewBox="0 0 32 33" fill="none" xmlns="http://www.w3.org/2000/svg">
                                        <path d="M16.457 2.32812C8.88472 2.32812 2.74268 7.69721 2.74268 14.313C2.77695 16.0809 3.21599 17.8157 4.02432 19.3772C4.83264 20.9387 5.98756 22.2831 7.39574 23.3017C7.13854 25.6316 6.30195 27.8544 4.96635 29.7567C4.96635 29.7567 9.86431 28.8317 11.9215 25.6243C13.3934 26.0749 14.921 26.3052 16.457 26.308C24.0292 26.308 30.1712 20.9389 30.1712 14.313C30.1712 7.68715 24.0292 2.32812 16.457 2.32812Z" stroke="#FAFAFA" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
                                    </svg> */}
                                    
                                    <Popup trigger={ <svg width="32" height="33" viewBox="0 0 32 33" fill="none" xmlns="http://www.w3.org/2000/svg">
                                        <path d="M16.457 2.32812C8.88472 2.32812 2.74268 7.69721 2.74268 14.313C2.77695 16.0809 3.21599 17.8157 4.02432 19.3772C4.83264 20.9387 5.98756 22.2831 7.39574 23.3017C7.13854 25.6316 6.30195 27.8544 4.96635 29.7567C4.96635 29.7567 9.86431 28.8317 11.9215 25.6243C13.3934 26.0749 14.921 26.3052 16.457 26.308C24.0292 26.308 30.1712 20.9389 30.1712 14.313C30.1712 7.68715 24.0292 2.32812 16.457 2.32812Z" stroke="#FAFAFA" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
                                    </svg>} position="right center">
                                        {close => (
                                            
                                        <div>
                                            <a className="close" onClick={close}>
                                            &times;
                                            </a>
                                            <SingleDesign getUserSavedImages={getUserSavedImages} part={design} image={image}  />
                                        
                                        </div>
                                        )}
                                     </Popup> 
                                </div>
                            </div>
                        </div>
                     </div>
                 </div>
                 <div className='designbottom'>
                     <div className='dicons'>
                       <img src={comunity} alt='comunity'  data-tip="These are the likes of our community"/>
                       <ReactTooltip />
                       <h6>
                       {image.com_likes&&
                            Math.abs(parseInt(image.com_likes.length))>=1000?
                            Math.sign(parseInt(image.com_likes.length))*((Math.abs(parseInt(image.com_likes.length))/1000).toFixed(1))+"k" :
                            Math.sign(parseInt(image.com_likes.length))*Math.abs(parseInt(image.com_likes.length))
                        }
                       </h6>
                     </div>
                     <div className='dicons'>
                       <img src={insta} alt='comunity' data-tip="These are the likes  at Instagram" />
                       <ReactTooltip />
                       <h6>
                       {image.insta_likes&&
                            Math.abs(parseInt(image.instaLikes))>=1000?
                            Math.sign(parseInt(image.instaLikes))*((Math.abs(parseInt(image.instaLikes))/1000).toFixed(1))+"k" :
                            Math.sign(parseInt(image.instaLikes))*Math.abs(parseInt(image.instaLikes))
                        }
                       </h6>
                     </div>
                     <div className='dicons'>
                       <img src={corp} alt='comunity' data-tip="These counts the manufactureres who participate"/>
                        <ReactTooltip />        
                        <h6>{design?design.factory_likes:"00"}</h6>
                     </div>
                     <div className='dicons'>
                       <img src={jury} alt='comunity' data-tip="These are the likes of the jury"/>
                       <ReactTooltip /> 
                       <h6>{image.jury_likes&&
                            Math.abs(parseInt(image.jury_likes.length))>=1000?
                            Math.sign(parseInt(image.jury_likes.length))*((Math.abs(parseInt(image.jury_likes.length))/1000).toFixed(1))+"k" :
                            Math.sign(parseInt(image.jury_likes.length))*Math.abs(parseInt(image.jury_likes.length))
                        }</h6>
                     </div>
                 </div>
             </div>
    </div>
  )
}

export default DesignComponent